ition:absolute;z-index:5;bottom:0;right:0;color:#fff;background-color:#656565;font-weight:800;border-radius:3px;width:110px;border:none;font-weight:800}pre{display:block;margin:-2px}#nextbutton:hover{background-color:#ff4500}#contactdiv{padding-left:2%;color:#000;background:url(/static/media/contactbackground.5bd72f5e.jpg) no-repeat center center fixed;height:100vh;background-size:cover}#contactdiv p{color:#000}#rowheading{margin:0}#aboutdiv{height:100vh;width:100%;background:url(/static/media/aboutbackground1.aa82f16b.jpg) no-repeat center center fixed;background-size:cover;color:#000}#floatingblock{float:left;text-align:left;display:block;height:auto;width:60%;background-color:rgba(255,255,255,.726);border-radius:20px;padding-left:0;line-height:2.2}@media only screen and (max-width:800px){#floatingblock{width:100%}}#codesite{float:left;z-index:5;margin-right:20px;color:#fff;background-color:#656565;font-weight:800;border-radius:3px;width:110px;border:none;font-weight:800}#codesite:hover{background-color:#ff4500}#adminblock{display:block;position:absolute;z-index:90;width:100%;height:100vh;background-image:linear-gradient(#fff,#fff)}#loginform{display:block;float:left;margin-top:100px;width:100%;margin-left:auto;height:250px;background:#d2cbcba4;color:#7e7e7e;box-shadow:#000;overflow:hidden}#rows{display:block;position:relative;border-bottom:#d0d0d0;border-bottom-style:solid;border-width:1px;padding:20px;overflow:hidden}
